import {
  getAllNodes,
  resetNodes,
  getUnvisitedNeighbors,
  sortNodesByDistance,
} from "./dijkstra.js";

/*finishDist is used here for the distance of the second dijkstra*/

export function bidijkstra(grid, startPos, finishPos) {
  document.getElementById("labelAlgorithm").innerHTML =
    "Bidirectional-Dijkstra";

  const visitedNodesInOrder = [];
  const visitedNodesInOrderBi = [];

  const algoGrid = resetNodes(grid);
  const algoGridBi = algoGrid.slice();

  const startNode = algoGrid[startPos.y][startPos.x];
  const finishNode = algoGrid[finishPos.y][finishPos.x];

  startNode.distance = 0;

  const startNodeBi = algoGridBi[finishPos.y][finishPos.x];
  const finishNodeBi = algoGridBi[startPos.x][startPos.x];
  startNodeBi.finishDistance = 0;

  const unvisitedNodes = getAllNodes(algoGrid);
  const unvisitedNodesBi = unvisitedNodes.slice();

  while (!!unvisitedNodes.length || !!unvisitedNodesBi.length) {
    sortNodesByDistance(unvisitedNodes);
    sortNodesByFinishDistance(unvisitedNodesBi);

    const closestNode = unvisitedNodes.shift();
    const closestNodeBi = unvisitedNodesBi.shift();

    if (closestNode.distance === Infinity) {
      console.log("no route");
    } else if (!(closestNode.type === "wall")) {
      console.log("push note");
      closestNode.isVisited = true;
      visitedNodesInOrder.push(closestNode);

      //if (!closestNode.finishDistance === Infinity) {return { visitedNodesInOrder, visitedNodesInOrderBi };}
      const finishNode2 = updateUnvisitedNeighbors(closestNode, algoGrid);
      if (!(finishNode2 === null)) {
        console.log("!finishNode === null:");
        console.log(finishNode);
        return {
          visitedNodesInOrder,
          visitedNodesInOrderBi,
          closestNode,
          finishNode2,
        };
      }
    }
    if (closestNodeBi.finishDistance === Infinity) {
      console.log("no route");
    } else if (!(closestNodeBi.type === "wall")) {
      console.log("push note Bi");
      closestNodeBi.isVisited = true;
      visitedNodesInOrderBi.push(closestNodeBi);

      //if (!closestNodeBi.distance === Infinity) {return { visitedNodesInOrder, visitedNodesInOrderBi };}
      const finishNode1 = updateUnvisitedNeighborsBi(closestNodeBi, algoGrid);
      if (!(finishNode1 === null)) {
        return {
          visitedNodesInOrder,
          visitedNodesInOrderBi,
          finishNode1,
          closestNodeBi,
        };
      }
    }
  }
  console.log("Not returned");
  console.log("Bi:");
  console.log(visitedNodesInOrderBi);
  console.log("Normal: ");
  console.log(visitedNodesInOrder);
}

function sortNodesByFinishDistance(unvisitedNodes) {
  unvisitedNodes.sort(
    (nodeA, nodeB) => nodeA.finishDistance - nodeB.finishDistance
  );
}

function updateUnvisitedNeighborsBi(node, grid) {
  const unvisitedNeighbors = getUnvisitedNeighbors(node, grid);
  for (const neighbor of unvisitedNeighbors) {
    const cost = neighbor.type === "road" ? 1 : 4;
    if (!neighbor.distance === Infinity) {
      return neighbor;
    } else if (neighbor.finishDistance >= node.finishDistance + cost) {
      neighbor.finishDistance = node.finishDistance + cost;
      neighbor.previousNode = node;
    }
  }
  return null;
}

function updateUnvisitedNeighbors(node, grid) {
  const unvisitedNeighbors = getUnvisitedNeighbors(node, grid);
  for (const neighbor of unvisitedNeighbors) {
    const cost = neighbor.type === "road" ? 1 : 4;
    if (!neighbor.finishDistance === Infinity) {
      return neighbor;
    } else if (neighbor.distance >= node.distance + cost) {
      neighbor.distance = node.distance + cost;
      neighbor.previousNode = node;
    }
  }
  return null;
}

export function getShortestPathBidirectional(finishNode1, finishNode2) {
  const shortestPath = [];
  let currentNode = finishNode1;

  while (currentNode !== null) {
    shortestPath.unshift(currentNode);
    currentNode = currentNode.previousNode;
  }

  currentNode = finishNode2;
  while (currentNode !== null) {
    shortestPath.unshift(currentNode);
    currentNode = currentNode.previousNode;
  }

  return shortestPath;
}
